import { createStore } from "redux";
import Storage from "../utils/Storage";

const initialStore = {
  configure: [
    {
      key: "platformName",
      value: "Android",
    },
    {
      key: "platformVersion",
      value: "Android 8.1",
    },
    {
      key: "deviceName",
      value: "Pixel XL API 27 2",
    },
    {
      key: "appPackage",
      value: "org.asdtm.goodweather",
    },
    {
      key: "appActivity",
      value: ".MainActivity",
    },
  ],
};

export default createStore((store = initialStore, action) => {
  const { configure } = store;
  const { type, payload } = action;

  switch (type) {
    case "update":
      const updatedData = payload;
      return {
        configure: configure.map((e) =>
          e.key === updatedData.key ? updatedData : e
        ),
      };

    default:
      return store;
  }
});
