import { connect } from "react-redux";
import { useState } from "react";
import { useHistory } from "react-router-dom";
import { Form, Input, Button } from "antd";
import "./Devicedata.css";
import Storage from "../../utils/Storage";

const layout = {
  labelCol: {
    span: 8,
  },
  wrapperCol: {
    span: 18,
  },
};

const validateMessages = {
  required: "${label} is required!",
};
/* eslint-enable no-template-curly-in-string */

function MyForm(props) {
  const headers = {
    "Accept": "application/json",
    "Content-Type": "application/json",
    "Access-Control-Allow-Origin": "*",
    "Access-Control-Allow-Headers":
      "Origin, X-Requested-With, Content-Type, Accept, Token",
    "Access-Control-Allow-Methods": "PUT,POST,GET,DELETE,OPTIONS",
    "token": Storage.getItem("token"),
  };

  const onFinish = (values) => {
    console.log("Success:", values);
  };

  const onFinishFailed = (errorInfo) => {
    console.log("Failed:", errorInfo);
  };

  const { configure, dispatch } = props;
  const history = useHistory();

  const [editedPlatformName, setEditedPlatformName] = useState(
    configure.find((n) => n.key === "platformName").value
  );
  const [editedPlatformVersion, setEditedPlatformVersion] = useState(
    configure.find((v) => v.key === "platformVersion").value
  );
  const [editedDeviceName, setEditedDeviceName] = useState(
    configure.find((d) => d.key === "deviceName").value
  );

  return (
    <Form
      className="device-configure-form"
      {...layout}
      name="device-configure-form"
      initialValues={{
        remember: true,
      }}
      onFinish={onFinish}
      onFinishFailed={onFinishFailed}
      validateMessages={validateMessages}
    >
      <Form.Item
        name={["deviceData", "platformName"]}
        label="platformName"
        initialValue={editedPlatformName}
        rules={[
          {
            required: true,
          },
        ]}
      >
        <Input onChange={(e) => setEditedPlatformName(e.target.value)} />
      </Form.Item>

      <Form.Item
        name={["deviceData", "plarformVersion"]}
        label="plarformVersion"
        initialValue={editedPlatformVersion}
        rules={[
          {
            required: true,
          },
        ]}
      >
        <Input onChange={(e) => setEditedPlatformVersion(e.target.value)} />
      </Form.Item>

      <Form.Item
        name={["deviceData", "deviceName"]}
        label="deviceName"
        initialValue={editedDeviceName}
        rules={[
          {
            required: true,
          },
        ]}
      >
        <Input onChange={(e) => setEditedDeviceName(e.target.value)} />
      </Form.Item>

      <Form.Item wrapperCol={{ ...layout.wrapperCol, offset: 8 }}>
        <Button
          type="primary"
          htmlType="submit"
          onClick={() => {
            dispatch({
              type: "update",
              payload: {
                key: "platformName",
                value: editedPlatformName,
              },
            });
            dispatch({
              type: "update",
              payload: {
                key: "platformVersion",
                value: editedPlatformVersion,
              },
            });
            dispatch({
              type: "update",
              payload: {
                key: "deviceName",
                value: editedDeviceName,
              },
            });
            const obj = {
              "platformName":editedPlatformName,
              "platformVersion":editedPlatformVersion,
              "deviceName":editedDeviceName,
            };
            fetch(process.env.REACT_APP_SERVER_PORT + "/configure/devicedata", {
              method: "post",
              body: JSON.stringify(obj),
              headers: headers
            }).then(res=>res.json())
            .then(data=>{console.log(data)})
            .catch(error=>console.log(error));
            history.goBack();
          }}
        >
          Submit
        </Button>
      </Form.Item>
    </Form>
  );
}

function Devicedata(props) {
  const { configure } = props;
  console.log(configure);
  return MyForm(props);
}

export default connect((state) => {
  return { configure: state.configure };
})(Devicedata);
