import { List } from "antd";
import { useEffect, useState } from "react";
import { DeleteOutlined } from "@ant-design/icons";
import dayjs from 'dayjs'
import Storage from "../../../utils/Storage";
import "./UserNotify.css";

export default function UserNotify() {
    const headers = {
        "Accept": "application/json",
        "Content-Type": "application/json",
        "Access-Control-Allow-Origin": "*",
        "Access-Control-Allow-Headers":
        "Origin, X-Requested-With, Content-Type, Accept, Token",
        "Access-Control-Allow-Methods": "PUT,POST,GET,DELETE,OPTIONS",
        "token": Storage.getItem("token"),
    };
    const IP = process.env.REACT_APP_SERVER_PORT;
    
    const [data, setData] = useState([]);

    useEffect(() => {
        fetch(IP + "/userMessage", {
            method: "get",
            headers: headers,
          })
            .then((res) => res.json())
            .then((newData) => {
              console.log(newData.value);
              setData(newData.value);
            })
            .catch((error) => console.log(error));   
    }, []);

    function showActions(apply_state, item){
        const showDelete = (
            <a
                onClick={() => {
                    setData(data.filter((d)=>d.apply_id != item.apply_id))
                    fetch(IP + "/userMessage/" + item.apply_id, {
                        method: "delete",
                        headers: headers,
                    })
                    .then((res) => res.json())
                    .then((news) => {
                        console.log(news);
                    })
                    .catch((error) => {
                        console.log(error);
                    });
                }}
            >
                <DeleteOutlined/>
            </a>
        )
        if(apply_state == 1){
            return [
                <p className = 'success'> 审核通过! </p>,
                <>
                    {showDelete}
                </>
            ]
        }
        else if(apply_state == 2){
            return [
                <p className = 'fail'> 审核失败! </p>,
                <>
                    {showDelete}
                </>
            ]
        }
        else if(apply_state == 0){
            return [
                <p className = 'wait'> 等待审核! </p>
            ]
        }
    }

    return (
        <div>
            <List
                itemLayout="horizontal"
                dataSource = {data}
                renderItem={item => (
                    <List.Item
                        actions = {showActions(item.apply_state, item)}
                    >
                        <List.Item.Meta
                            title = {<p className="list-title">{item.apply_descriptions + "（" + item.apply_path + "）"}</p>}
                            description = { 
                                item.apk_name + "/ " + dayjs(item.apply_time).format("YYYY-MM-DD")
                            }
                        />
                    </List.Item>
                  )
                }
            />
                
               
                
        </div>
    )
}