import { Table, Input, Button, Space } from "antd";
import { connect } from "react-redux";
import Highlighter from "react-highlight-words";
import { useState, useEffect } from "react";
import { useHistory } from "react-router-dom";
import { SearchOutlined } from "@ant-design/icons";
import "./Appdata.css";
import Storage from "../../utils/Storage";

function Appdata(props) {
  const headers = {
    "Accept": "application/json",
    "Content-Type": "application/json",
    "Access-Control-Allow-Origin": "*",
    "Access-Control-Allow-Headers":
    "Origin, X-Requested-With, Content-Type, Accept, Token",
    "Access-Control-Allow-Methods": "PUT,POST,GET,DELETE,OPTIONS",
    "token": Storage.getItem("token"),
  };
  const [appdata, setAppdata] = useState("");
  const [searchText, setSearchText] = useState("");
  const [searchedColumn, setSearchColumn] = useState("");
  const { configure, dispatch } = props;
  const history = useHistory();
  //const {Column} = Table;

  useEffect(() => {
    fetch(process.env.REACT_APP_SERVER_PORT + "/specified/allapps", {
      method: "get",
    })
      .then((res) => res.json())
      .then((data) => {
        setAppdata(data.value);
      })
      .catch((error) => console.log(error));
  }, [configure]);

  const handleSearch = (selectedKeys, confirm, dataIndex) => {
    confirm();
    setSearchText(selectedKeys[0]);
    setSearchColumn(dataIndex);
    console.log(selectedKeys[0]);
  };

  const handleReset = (clearFilters) => {
    clearFilters();
    setSearchText("");
  };

  const getColumnSearchProps = dataIndex => ({
    filterDropdown: ({
      setSelectedKeys,
      selectedKeys,
      confirm,
      clearFilters,
    }) => (
      <div style={{ padding: 8 }}>
        <Input
          placeholder={'Search ${dataIndex}'}
          value={selectedKeys[0]}
          onChange={(e) =>
            setSelectedKeys(e.target.value ? [e.target.value] : [])
          }
          onPressEnter={() => handleSearch(selectedKeys, confirm, dataIndex)}
          style={{ marginBottom: 8, display: "block" }}
        />
        <Space>
          <Button
            type="primary"
            onClick={() => handleSearch(selectedKeys, confirm, dataIndex)}
            icon={<SearchOutlined />}
            size="small"
            style={{ width: 90 }}
          >
            Search
          </Button>
          <Button
            onClick={() => handleReset(clearFilters)}
            size="small"
            style={{ width: 90 }}
          >
            Reset
          </Button>
          <Button
            type="link"
            size="small"
            onClick={() => {
              confirm({ closeDropdown: false });
              setSearchText(selectedKeys[0]);
              setSearchColumn(dataIndex);
            }}
          >
            Filter
          </Button>
        </Space>
      </div>
    ),
    filterIcon: (filtered) => (
      <SearchOutlined style={{ color: filtered ? "#1890ff" : undefined }} />
    ),
    onFilter: (value, record) =>
      record[dataIndex]
        ? record[dataIndex]
            .toString()
            .toLowerCase()
            .includes(value.toLowerCase())
        : "",
    onFilterDropdownVisibleChange: (visible) => {
      if (visible) {
        //setTimeout(() => searchInput.select(), 100);
      }
    },
    render: (text) =>
      searchedColumn === dataIndex ? (
        <Highlighter
          highlightStyle={{ backgroundColor: "#ffc069", padding: 0 }}
          searchWords={[searchText]}
          autoEscape
          textToHighlight={text ? text.toString() : ""}
        />
      ) : (
        text
      ),
  });

  const columns = [
    {
      title: "app_id",
      dataIndex: "app_id",
      key: "app_id",
      className: "app_id",
      sorter: (a, b) => a.app_id - b.app_id,
      ...getColumnSearchProps("app_id"),
    },
    {
      title: "apk_name",
      dataIndex: "apk_name",
      key: "apk_name",
      className: "apk_name",
      sorter: (a, b) => a.apk_name > b.apk_name,
      ...getColumnSearchProps("apk_name"),
    },
    {
      title: "package_name",
      dataIndex: "package_name",
      key: "package_name",
      className: "package_name",
    },
    {
      title: "version",
      dataIndex: "version",
      key: "version",
      className: "version",
    },
    {
      title: "Action",
      key: "action",
      classname: "app-choose",
      render: (text, record) => (
        <a
          className="choose"
          onClick={() => {
            dispatch({
              type: "update",
              payload: {
                key: "appPackage",
                value: record.package_name,
              },
            });
            fetch(process.env.REACT_APP_SERVER_PORT + "/configure/apkdata", {
              method: "post",
              body: record.apk_name,
              headers: headers,
            })
              .then((res) => res.json())
              .then((data) => {
                console.log(data);
              })
              .catch((error) => console.log(error));
            history.goBack();
            console.log(record);
          }}
        >
          choose
        </a>
      ),
    },
  ];
  return (
    <div className="appdata_page">
      <Table
        className="appdata_table"
        columns={columns}
        dataSource={appdata}
      ></Table>
    </div>
  );
}

export default connect((state) => {
  return { configure: state.configure };
})(Appdata);
