import {
  BrowserRouter,
  Switch,
  Link,
  Redirect,
  useHistory,
} from "react-router-dom";
import "antd/dist/antd.css";
import { Layout, Menu, Dropdown, Avatar } from "antd";
import {
  Configure,
  Ifml,
  Devicedata,
  Appdata,
  Home,
  Download,
  User,
  Methods,
  ScenarioCheck,
} from "./pages";
import {
  AppstoreOutlined,
  MailOutlined,
  PictureOutlined,
  DownloadOutlined,
  ExportOutlined,
  UserOutlined,
} from "@ant-design/icons";
import "./App.css";
import React, { useState } from "react";
import { Footer } from "antd/lib/layout/layout";
import AuthRoute from "./utils/AuthRoute";
import Storage from "./utils/Storage";
import AdminRoute from "./utils/AdminRoute";

const { Header, Content } = Layout;

function App() {
  const headers = {
    "Accept": "application/json",
    "Content-Type": "application/json",
    "Access-Control-Allow-Origin": "*",
    "Access-Control-Allow-Headers":
    "Origin, X-Requested-With, Content-Type, Accept, Token",
    "Access-Control-Allow-Methods": "PUT,POST,GET,DELETE,OPTIONS",
    "token": Storage.getItem("token"),
  };
  const [mycollapsed, setCollapsed] = useState(false);
  const toggle = () => {
    setCollapsed(!mycollapsed);
  };
  const history = useHistory();

  const menu = (
    <Menu>
      <Menu.Item icon={<UserOutlined />}>
        <Link to="/app/user">个人信息</Link>
      </Menu.Item>
      <Menu.Item icon={<ExportOutlined />} onClick={()=>{
        fetch(
          process.env.REACT_APP_SERVER_PORT + "/logout",
          {
            method: "post",
            headers: headers,
          }
        ).then(res => res.json())
        .then(data => {
          console.log(data.value);
        })
        .catch(error=>console.log(error));
        Storage.clear();
        history.goBack();
      }}>
        退出登陆
      </Menu.Item>
    </Menu>
  );
  //const props = this.props;
  if (Storage.getItem("admin") === true) {
    return (
      <BrowserRouter>
        <Layout className="site-layout">
          <Header className="site-layout-background" style={{ padding: 0 }}>
            <Menu
              theme="light"
              mode="horizontal"
              defaultSelectedKeys={["1"]}
              triggerSubMenuAction={["click"]}
            >
              <Menu.Item key="1" icon={<AppstoreOutlined />}>
                <Link to="/app/home">主页</Link>
              </Menu.Item>
              <Menu.Item key="8" icon={<DownloadOutlined />}>
                <Link to="/app/scenariocheck">场景审核</Link>
              </Menu.Item>
              <Dropdown
                overlay={menu}
                className="site-layout-avatar"
                trigger={["click"]}
              >
                <Avatar size={48} icon={<UserOutlined />} />
              </Dropdown>
            </Menu>
          </Header>
          <Content
            className="site-layout-background"
            style={{
              margin: "10px 0px",
              padding: "0px 100px",
              minHeight: 782,
            }}
          >
            <Switch>
              <AuthRoute path="/app/home" exact component={Home} />
              <AdminRoute
                path="/app/scenariocheck"
                exact
                component={ScenarioCheck}
              />
              <AuthRoute path="/app/user" exact component={User} />
              <Redirect to="/app/home" />
            </Switch>
          </Content>
          <Footer style={{ textAlign: "center" }}>
            Created by Script Generated Group <br />
            Members: Ge Yu, Ma Hangyu, Wang Jiayi, Wang Xiran, Wang You, Wang Zibo <br />
            Mentors: Xie Linna, Zhang Tian
          </Footer>
        </Layout>
      </BrowserRouter>
    );
  } else {
    return (
      <BrowserRouter>
        <Layout className="site-layout">
          <Header className="site-layout-background" style={{ padding: 0 }}>
            <Menu
              theme="light"
              mode="horizontal"
              defaultSelectedKeys={["1"]}
              // defaultOpenKeys={["sub1"]}
              triggerSubMenuAction={["click"]}
            >
              <Menu.Item key="1" icon={<AppstoreOutlined />}>
                <Link to="/app/home">主页</Link>
              </Menu.Item>
              <Menu.Item key="2" icon={<AppstoreOutlined />}>
                <Link to="/app/configure">配置信息</Link>
              </Menu.Item>
              {/* <Menu.Item key="3" icon={<PictureOutlined />}>
                <Link to="/app/ifml">可视化视图</Link>
              </Menu.Item> */}
              <Menu.Item key="4" icon={<MailOutlined />}>
                <Link to="/app/methods">生成策略</Link>
              </Menu.Item>
              <Menu.Item key="7" icon={<DownloadOutlined />}>
                <Link to="/app/download">下载管理</Link>
              </Menu.Item>
              {/* <Menu.Item key="8" icon={<DownloadOutlined />}>
                <Link to="/app/scenariocheck">场景审核</Link>
              </Menu.Item> */}
              <Dropdown
                overlay={menu}
                className="site-layout-avatar"
                trigger={["click"]}
              >
                <Avatar size={48} icon={<UserOutlined />} />
              </Dropdown>
            </Menu>
          </Header>
          <Content
            className="site-layout-background"
            style={{
              margin: "10px 0px",
              padding: "0px 100px",
              minHeight: 782,
            }}
          >
            <Switch>
              <AuthRoute path="/app/home" exact component={Home} />
              <AuthRoute
                path="/app/configure/devicedata"
                exact
                component={Devicedata}
              />
              <AuthRoute
                path="/app/configure/appdata"
                exact
                component={Appdata}
              />
              <AuthRoute path="/app/configure" component={Configure} />
              <AuthRoute path="/app/ifml" exact component={Ifml} />
              <AuthRoute path="/app/methods" component={Methods} />
              <AuthRoute path="/app/download" component={Download} />
              <AdminRoute
                path="/app/scenariocheck"
                exact
                component={ScenarioCheck}
              />
              <AuthRoute path="/app/user" exact component={User} />
              <Redirect to="/app/home" />
            </Switch>
          </Content>
          <Footer style={{ textAlign: "center" }}>
            Created by Script Generated Group <br />
            Members: Ge Yu, Ma Hangyu, Wang Jiayi, Wang Xiran, Wang You, Wang Zibo <br />
            Mentors: Xie Linna, Zhang Tian
          </Footer>
        </Layout>
      </BrowserRouter>
    );
  }
}

export default App;
