import { Descriptions, Button} from "antd"
import { connect } from "react-redux";
import { useHistory } from "react-router";
import { useEffect } from "react";
import Storage from "../../utils/Storage";
import "./Configure.css";

function Configure(props) {
    const headers = {
        "Accept": "application/json",
        "Content-Type": "application/json",
        "Access-Control-Allow-Origin": "*",
        "Access-Control-Allow-Headers":
        "Origin, X-Requested-With, Content-Type, Accept, Token",
        "Access-Control-Allow-Methods": "PUT,POST,GET,DELETE,OPTIONS",
        "token": Storage.getItem("token"),
    };
    const history = useHistory()
    const {configure, dispatch} = props;
    console.log(props)
    useEffect(() => {
        fetch(process.env.REACT_APP_SERVER_PORT + "/getappium", {
            method: "get",
            headers: headers,
        }).then((res) => res.json())
        .then((data) => {
            data.value.map(e=>{
                dispatch({
                    type: "update",
                    payload: {
                        key: e.key,
                        value: e.value,
                    },
                });
            })
            console.log(data.value);
        });  
    }, [])
    return (
        <div>
            <Descriptions
                title="Appium 配置信息"
                className = "appium-data"
                bordered={true}
                column = {1}
            >
                <Descriptions.Item label="platformName">
                    {configure.find((n) => n.key === "platformName").value}
                </Descriptions.Item>
                <Descriptions.Item label="platformVersion">
                    {configure.find((v) => v.key === "platformVersion").value}
                </Descriptions.Item>
                <Descriptions.Item label="deviceName">
                    {configure.find((d) => d.key === "deviceName").value}
                </Descriptions.Item>
                <Descriptions.Item label="appPackage">
                    {configure.find((d) => d.key === "appPackage").value}
                </Descriptions.Item>
                <Descriptions.Item label="appActivity">
                    {configure.find((d) => d.key === "appActivity").value}
                </Descriptions.Item>
            </Descriptions>
            <div className="buttons">
                <Button
                    className="device-data"
                    size="large"
                    onClick={()=>
                        history.push("/app/configure/devicedata")
                    }
                >
                    修改配置信息
                </Button>
                &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
                &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
                <Button 
                    className="app-data"
                    size="large"
                    onClick={()=>
                    history.push("/app/configure/appdata")
                    }
                >
                    选择App
                </Button>
            </div>
        </div>

    )
}

export default connect((state) => {
    return { configure: state.configure };
})(Configure);