import { React, useState } from "react";
import { Input, Button } from "antd";
import { useHistory } from "react-router-dom";
import { connect } from "react-redux";
import {
  UserOutlined,
  LockOutlined,
  EyeInvisibleOutlined,
  EyeTwoTone,
} from "@ant-design/icons";
import Storage from "../../utils/Storage";
import img from "../../images/logo.png"
import "./LogIn.css";

function LogIn(props) {
  const IP = process.env.REACT_APP_SERVER_PORT;
  const headers = {
    Accept: "application/json",
    "Content-Type": "application/json",
    "Access-Control-Allow-Origin": "*",
    "Access-Control-Allow-Headers":
      "Origin, X-Requested-With, Content-Type, Accept, Token",
    "Access-Control-Allow-Methods": "PUT,POST,GET,DELETE,OPTIONS",
  };

  const [username, setUsername] = useState("");
  const [password, setPassword] = useState("");
  const history = useHistory();

  return (
    <div
      className="login page"
      onKeyUp={(e) => {
        if (e.key == "Enter" && username != "" && password != "") {
          const obj = {
            userName: username,
            password: password,
          };
          fetch(IP + "/login", {
            method: "post",
            body: JSON.stringify(obj),
            headers: headers,
          })
            .then((res) => res.json())
            .then((news) => {
              const { value } = news;
              if (value.token == "failure") {
                alert("用户名或密码错误！");
              } else {
                Storage.clear();
                Storage.setItem("token", value.token, 60 * 60 * 1000);
                Storage.setItem("admin", value.admin, 60 * 60 * 1000);
                updateAppium(props);
                history.push("/app/home");
              }
            })
            .catch((error) => console.log(error));
        }
      }}
    >
      <div className = "logo_wrap">
        <img src={img}  width = "400px"/>
      </div>
      <div className="login_wrap">
        <div className="login_title">LOG IN</div>
        <div className="line"></div>
        <div className="login_form">
          <Input
            className="input_name"
            onChange={(e) => {
              setUsername(e.target.value);
            }}
            size="large"
            placeholder="username"
            prefix={<UserOutlined />}
          />
          <Input.Password
            className="input_password"
            onChange={(e) => {
              setPassword(e.target.value);
            }}
            size="large"
            placeholder="password"
            prefix={<LockOutlined />}
            iconRender={(visible) =>
              visible ? <EyeTwoTone /> : <EyeInvisibleOutlined />
            }
          />
          <Button
            className="login_button"
            size="large"
            type="primary"
            onClick={() => {
              if (username == "") {
                alert("请输入用户名！");
              } else if (password == "") {
                alert("请输入密码！");
              } else {
                const obj = {
                  userName: username,
                  password: password,
                };
                fetch(IP + "/login", {
                  method: "post",
                  body: JSON.stringify(obj),
                  headers: headers,
                })
                  .then((res) => res.json())
                  .then((news) => {
                    const { value } = news;
                    if (value.token == "failure") {
                      alert("用户名或密码错误！");
                    } else {
                      Storage.clear();
                      Storage.setItem("token", value.token, 60 * 60 * 1000);
                      Storage.setItem("admin", value.admin, 60 * 60 * 1000);
                      console.log(value.admin);
                      updateAppium(props);
                      history.push("/app/home");
                    }
                  })
                  .catch((error) => console.log(error));
              }
            }}
          >
            LOG IN
          </Button>
          <div> Don't have an account?</div>
          <div>
            <a onClick={() => history.push("/signup")}>Sign up</a>
          </div>
        </div>
      </div>
    </div>
  );
}

function updateAppium(props) {
  const { dispatch } = props;
  const headers = {
    Accept: "application/json",
    "Content-Type": "application/json",
    "Access-Control-Allow-Origin": "*",
    "Access-Control-Allow-Headers":
      "Origin, X-Requested-With, Content-Type, Accept, Token",
    "Access-Control-Allow-Methods": "PUT,POST,GET,DELETE,OPTIONS",
    token: Storage.getItem("token"),
  };
  fetch(process.env.REACT_APP_SERVER_PORT + "/getappium", {
    method: "get",
    headers: headers,
  })
    .then((res) => res.json())
    .then((data) => {
      data.value.map((e) => {
        dispatch({
          type: "update",
          payload: {
            key: e.key,
            value: e.value,
          },
        });
      });
      console.log(data.value);
    });
}

export default connect((state) => {
  return { configure: state.configure };
})(LogIn);
