import { Button, notification } from "antd";
import { connect } from "react-redux";
import "./FullPath.css";
import Storage from "../../../utils/Storage";

function FullPath(props) {
  const headers = {
    Accept: "application/json",
    "Content-Type": "application/json",
    "Access-Control-Allow-Origin": "*",
    "Access-Control-Allow-Headers":
      "Origin, X-Requested-With, Content-Type, Accept, Token",
    "Access-Control-Allow-Methods": "PUT,POST,GET,DELETE,OPTIONS",
    token: Storage.getItem("token"),
  };

  return (
    <div className="fullpath-page">
      <div className="intro">点击下方按钮即可生成全路径测试脚本</div>
      <div className="fullpath">
        <Button
          className="btn-fullpath"
          size="large"
          onClick={() => {
            notification["info"]({
              message: "开始生成",
              description: "您可以继续发起其他任务，本任务完成后我们会通知您",
              duration: 3,
            });
            fetch(process.env.REACT_APP_SERVER_PORT + "/AllTraverse", {
              method: "post",
              body: JSON.stringify("mhy"),
              headers: headers,
            })
              .then((res) => res.json())
              .then((data) => {
                console.log(data);
                notification["success"]({
                  message: "生成成功",
                  description: "请在下载管理页面下载所生成的脚本",
                  duration: 3,
                });
              })
              .catch((error) => {
                console.log(error);
                notification["error"]({
                  message: "生成失败",
                  description: "请将以下错误原因反馈给管理员：" + error,
                  duration: 3,
                });
              });
          }}
        >
          点击生成全路径测试脚本
        </Button>
      </div>
    </div>
  );
}

export default connect((state) => {
  return { configure: state.configure };
})(FullPath);
