import { useEffect, useState } from "react";
import { connect } from "react-redux";
import { Form, Select, Button, Checkbox, notification } from "antd";
import { useHistory } from "react-router-dom";
import "./SpecifiedNode.css";
import Storage from "../../../utils/Storage";

function onFinish(values) {
  console.log("Received values from form: ", values);
}

function SpecifiedNode(props) {
  const headers = {
    Accept: "application/json",
    "Content-Type": "application/json",
    "Access-Control-Allow-Origin": "*",
    "Access-Control-Allow-Headers":
      "Origin, X-Requested-With, Content-Type, Accept, Token",
    "Access-Control-Allow-Methods": "PUT,POST,GET,DELETE,OPTIONS",
    token: Storage.getItem("token"),
  };
  const { configure } = props;
  const [stateSum, setStateSum] = useState(0);
  useEffect(() => {
    fetch(process.env.REACT_APP_SERVER_PORT + "/specified/totalState", {
      method: "get",
      headers: headers,
    })
      .then((res) => res.json())
      .then((data) => {
        setStateSum(data.value);
      });
  }, [configure]);

  const [srcNode, setSrcNode] = useState(
    Storage.getItem("specifiedNodeSrcNode") == null
      ? 0
      : Storage.getItem("specifiedNodeSrcNode")
  );
  const [dstNode, setDstNode] = useState(
    Storage.getItem("specifiedNodeDstNode") == null
      ? 0
      : Storage.getItem("specifiedNodeDstNode")
  );
  const [shortestPath, setShortestPath] = useState(
    Storage.getItem("specifiedNodeShortestPath") == null
      ? stateSum >= 50
      : Storage.getItem("specifiedNodeShortestPath")
  );
  const [passList, setPassList] = useState(
    Storage.getItem("specifiedNodePassList") == null
      ? []
      : Storage.getItem("specifiedNodePassList")
  );
  const [notPassList, setNotPassList] = useState(
    Storage.getItem("specifiedNodeNotPassList") == null
      ? []
      : Storage.getItem("specifiedNodeNotPassList")
  );
  const [form] = Form.useForm();

  const { Option } = Select;
  const options = Array.from({ length: stateSum + 1 }, (x, i) => i).map((a) => (
    <Option key={a}>{a}</Option>
  ));
  const validateMessages = {
    required: "${label} is required!",
  };

  console.log(passList.length);
  console.log(notPassList.length);

  return (
    <Form
      name="specifiedNodeForm"
      layout="vertical"
      onFinish={onFinish}
      validateMessages={validateMessages}
    >
      <Form.Item
        name="srcNode"
        label="源节点"
        rules={[{ required: true }]}
        initialValue={srcNode}
      >
        <Select
          style={{ width: 200 }}
          onChange={(v) => {
            setSrcNode(v);
            Storage.setItem("specifiedNodeSrcNode", v, 60 * 60 * 1000);
          }}
        >
          {options}
        </Select>
      </Form.Item>

      <Form.Item
        name="tgtNode"
        label="目的节点"
        rules={[{ required: true }]}
        initialValue={dstNode}
      >
        <Select
          style={{ width: 200 }}
          onChange={(v) => {
            setDstNode(v);
            Storage.setItem("specifiedNodeDstNode", v, 60 * 60 * 1000);
          }}
        >
          {options.filter((o) => o.key !== srcNode)}
        </Select>
      </Form.Item>

      <Form.Item name="shorestPath" initialValue={shortestPath}>
        <Checkbox
          checked={shortestPath}
          onChange={(e) => {
            setShortestPath(e.target.checked);
            Storage.setItem(
              "specifiedNodeShortestPath",
              e.target.checked,
              60 * 60 * 1000
            );
          }}
          disabled={
            !(
              passList.length === 0 &&
              notPassList.length === 0 &&
              stateSum < 50
            )
          }
        >
          {stateSum >= 50
            ? "节点数大于50，只能生成最短路径"
            : passList.length === 0 && notPassList.length === 0
            ? "生成路径最短脚本"
            : "已自定义经过/不经过节点，无法生成路径最短脚本"}
        </Checkbox>
      </Form.Item>

      <Form.Item name="pns" label="经过的节点" initialValue={passList}>
        <Select
          mode="multiple"
          allowClear
          style={{ width: "100%" }}
          placeholder={
            shortestPath
              ? "生成路径最短脚本时无法选择经过的节点"
              : "请选择经过的节点"
          }
          defaultValue={[]}
          onChange={(e) => {
            setPassList(e);
            Storage.setItem("specifiedNodePassList", e, 60 * 60 * 1000);
          }}
          disabled={shortestPath}
        >
          {options.filter((o) => {
            return (
              !notPassList.includes(o.key) &&
              o.key !== srcNode &&
              o.key !== dstNode
            );
          })}
        </Select>
      </Form.Item>

      <Form.Item name="npns" label="Not Pass Nodes" initialValue={notPassList}>
        <Select
          mode="multiple"
          allowClear
          style={{ width: "100%" }}
          placeholder={
            shortestPath
              ? "生成路径最短脚本时无法选择不经过的节点"
              : "请选择不经过的节点"
          }
          defaultValue={[]}
          onChange={(e) => {
            setNotPassList(e);
            Storage.setItem("specifiedNodeNotPassList", e, 60 * 60 * 1000);
          }}
          disabled={shortestPath}
        >
          {options.filter((o) => {
            return (
              !passList.includes(o.key) && o.key != srcNode && o.key != dstNode
            );
          })}
        </Select>
      </Form.Item>

      <Form.Item style={{ textAlign: "center" }}>
        <Button
          type="primary"
          htmlType="submit"
          onClick={(e) => {
            notification["info"]({
              message: "开始生成",
              description: "您可以继续发起其他任务，本任务完成后我们会通知您",
              duration: 3,
            });
            const obj = {
              src: srcNode,
              tgt: dstNode,
              isBFS: shortestPath,
              pl: passList,
              npl: notPassList,
            };
            fetch(process.env.REACT_APP_SERVER_PORT + "/specified/sg", {
              method: "post",
              headers: headers,
              body: JSON.stringify(obj),
            })
              .then((res) => res.json())
              .then((data) => {
                notification["success"]({
                  message: "生成成功",
                  description: "请在下载管理页面下载所生成的脚本",
                  duration: 3,
                });
              })
              .catch((error) => {
                notification["error"]({
                  message: "生成失败",
                  description: "请将以下错误原因反馈给管理员：" + error,
                  duration: 3,
                });
              });
          }}
        >
          生成
        </Button>
        <Button
          htmlType="button"
          onClick={() => {
            Storage.removeItem("specifiedNodeSrcNode");
            Storage.removeItem("specifiedNodeDstNode");
            Storage.removeItem("specifiedNodeShortestPath");
            Storage.removeItem("specifiedNodePassList");
            Storage.removeItem("specifiedNodeNotPassList");
            window.location.reload();
          }}
        >
          重置
        </Button>
      </Form.Item>
    </Form>
  );
}

export default connect((state) => {
  return { configure: state.configure };
})(SpecifiedNode);
