import { BrowserRouter, Switch, Link } from "react-router-dom";
import { Layout, Menu } from "antd";
import { connect } from "react-redux";
import { ToolOutlined } from "@ant-design/icons";
import Scenario from "./Scenario/Scenario";
import SpecifiedNode from "./SpecifiedNode/SpecifiedNode";
import FullPath from "./FullPath/FullPath";
import SpecifiedPath from "./SpecifiedPath/SpecifiedPath";
import AuthRoute from "../../utils/AuthRoute";

import "./Methods.css";
import { configure } from "@testing-library/dom";

const { Content, Sider } = Layout;

function Methods(props) {
  const { configure } = props;
  return (
    <BrowserRouter>
      <Layout className="site-layout-background" style={{ padding: "0px 0px" }}>
        <Sider className="site-layout-background" width={200}>
          <Menu
            mode="vertical"
            defaultSelectedKeys={["1"]}
            style={{ height: "100%" }}
          >
            <Menu.Item key="1" icon={<ToolOutlined />}>
              <Link to="/app/methods/scenario">场景搜索</Link>
            </Menu.Item>
            <Menu.Item key="2" icon={<ToolOutlined />}>
              <Link to="/app/methods/specifiednode">指定节点</Link>
            </Menu.Item>
            <Menu.Item key="3" icon={<ToolOutlined />}>
              <Link to="/app/methods/fullpath">全路径</Link>
            </Menu.Item>
            <Menu.Item key="4" icon={<ToolOutlined />}>
              <Link to="/app/methods/specifiedpath">指定路径</Link>
            </Menu.Item>
          </Menu>
        </Sider>
        <Content
          className="site-layout-background"
          style={{
            margin: "0px 16px",
            padding: 24,
            minHeight: 758,
          }}
        >
          <Switch>
            <AuthRoute path="/app/methods/" exact component={Scenario} />
            <AuthRoute
              path="/app/methods/scenario"
              exact
              component={Scenario}
            />
            <AuthRoute
              path="/app/methods/fullpath"
              exact
              component={FullPath}
            />
            <AuthRoute
              path="/app/methods/specifiednode"
              exact
              component={SpecifiedNode}
            />
            <AuthRoute
              path="/app/methods/specifiedpath"
              exact
              component={SpecifiedPath}
            />
          </Switch>
        </Content>
      </Layout>
    </BrowserRouter>
  );
}

export default connect((state) => {
  return { configure: state.configure };
})(Methods);
