import { Input, List, Button, Avatar, Modal, Form } from "antd";
import { useEffect, useState } from "react";
import { ReloadOutlined, DownloadOutlined, DeleteOutlined, FileGifOutlined, PlusCircleOutlined, CloseOutlined } from "@ant-design/icons";
import { connect } from "react-redux";
import "./Download.css";
import img from "../../images/download.png"
import Storage from "../../utils/Storage";
import dayjs from 'dayjs'

function Download(props) {
  const headers = {
    "Accept": "application/json",
    "Content-Type": "application/json",
    "Access-Control-Allow-Origin": "*",
    "Access-Control-Allow-Headers":
    "Origin, X-Requested-With, Content-Type, Accept, Token",
    "Access-Control-Allow-Methods": "PUT,POST,GET,DELETE,OPTIONS",
    "token": Storage.getItem("token"),
  };
  const [username, setUsername] = useState("");
  const [image, setImage] = useState("");
  const [isModalVisible1, setIsModalVisible1] = useState(false);
  const [isModalVisible2, setIsModalVisible2] = useState(false);
  const [downloadinfo, setDownloadinfo] = useState("");
  const [filterdownloadinfo, setFilterdownloadinfo] = useState("");
  const [searchkey, setSearchkey] = useState("");
  const [apkname, setApkname] = useState("");
  const [filename, setFilename] = useState("");
  const [path, setPath] = useState("");
  const [scenarioname, setScenarioname] = useState("");
  const { Search } = Input;
  const { configure } = props;
  const handleCancel1 = () => {
    setIsModalVisible1(false);
    setImage("");
  };
  const handleCancel2 = () => {
    setIsModalVisible2(false);
    setScenarioname("");
  };
  const handleOk2 = () => {
    if (scenarioname == ""){
      alert("申请场景名不为空");
    }
    else{
      console.log(filename);
      fetch(
        process.env.REACT_APP_SERVER_PORT + "/applyForScenerio",
        {
          method: "post",
          body: JSON.stringify(
            {
              fileName: filename,
              path: path,
              apkName: apkname,
              description: scenarioname,
            }
          ),
          headers: headers,
        }
      ).then(res => res.json())
      .then(data => {
        console.log(data.value);
        if (data.value)
          alert("场景添加成功");
      })
      .catch(error=>console.log(error));
      setIsModalVisible2(false);
    }
  };
  console.log(headers);
  useEffect(() => {
    fetch(process.env.REACT_APP_SERVER_PORT + "/download", {
      method: "get",
      headers: headers,
      
    })
      .then((res) => res.json())
      .then((data) => {
        console.log(data.value);
        setDownloadinfo(data.value);
        setFilterdownloadinfo(data.value);
      });
  }, []);
  useEffect(() => {
    fetch(process.env.REACT_APP_SERVER_PORT + "/userInfo", {
        method: "get",
        headers: headers,
    }).then((res) => res.json())
    .then((data) => {
        setUsername(data.value);
    });
  }, []);

  return (
    <div>
      <Button
        icon={<ReloadOutlined />} 
        onClick={() => {
          setFilterdownloadinfo(downloadinfo); setSearchkey("");
        }}
      />
      <Search
        className="search"
        value={searchkey}
        placeholder="搜索文件名"
        onSearch={
          (e) => {
            
            setFilterdownloadinfo(downloadinfo.filter((d) => d.scriptName.indexOf(e) != -1));
          }
        }
        onChange={
          (e) => {
            console.log(e.target.value);
            setSearchkey(e.target.value);
          }
        }
      />
      <List
        className="downloadManager"
        itemLayout="horizontal"
        pagination={{
          onChange: (page, pageSize) => {
            console.log(page, pageSize);
          },
          pageSize: 8,
          showSizeChanger: false,
          showQuickJumper: true,
        }}
        dataSource={filterdownloadinfo}
        renderItem={(item) => (
          <List.Item
            actions={[
              <a
                key="py-download"
                onClick={() => {
                  const eleLink = document.createElement("a");
                  eleLink.style.display = "none";
                  console.log(item.apkName);
                  eleLink.href =
                    process.env.REACT_APP_SERVER_PORT +
                    "/download/" +
                    item.apkName +
                    "/" +
                    item.scriptName +
                    "/" +
                    username;
                  document.body.appendChild(eleLink);
                  eleLink.click();
                  document.body.removeChild(eleLink);
                }}
              >
                <DownloadOutlined 
                  title="下载"
                />
              </a>,
              <a
                key="py-delete"
                onClick={() => {
                  fetch(
                    process.env.REACT_APP_SERVER_PORT +
                      "/delete/" +
                      item.apkName +
                      "/" +
                      item.scriptName,
                    {
                      method: "get",
                      headers: headers,
                    }
                  )
                    .then((res) => res.json())
                    .then((data) => {
                      console.log(data);
                    })
                    .catch((error) => console.log(error));
                  setDownloadinfo(downloadinfo.filter((d) => (d.apkName + d.scriptName) !== (item.apkName + item.scriptName)));
                  setFilterdownloadinfo(filterdownloadinfo.filter((d) => (d.apkName + d.scriptName) !== (item.apkName + item.scriptName)));
                  console.log(downloadinfo);
                  console.log(filterdownloadinfo);
                  //setFlag(!flag);
                  alert(item.apkName + "-" + item.scriptName +"删除成功");
                  // fetch(process.env.REACT_APP_SERVER_PORT + "/download", {
                  //   method: "get",
                  //   headers: headers,
                  // })
                  //   .then((res) => res.json())
                  //   .then((data) => {
                  //     setDownloadinfo(data.value);
                  //   });
                }}
                //onClick
              >
                <DeleteOutlined 
                  title="删除"
                />
              </a>,
              <a
                key="py-display"
                onClick={ () => {
                  setIsModalVisible1(true);
                  fetch(process.env.REACT_APP_SERVER_PORT + "/display/" + item.apkName + "/" + item.scriptName, {
                    method: "get",
                    headers: headers,
                  }).then((res) => res.text())
                  .then((data) => {
                    setImage("data:image/gif;base64,"+data);
                    //console.log(data);
                    
                  });
                  console.log(image);
                  console.log(downloadinfo);
                  console.log(filterdownloadinfo);
                }}
              >
                <FileGifOutlined 
                  title="gif展示"
                />
              </a>,
              <a
                key="scenario-add"
                onClick={ () => {
                  setIsModalVisible2(true);
                  setApkname(item.apkName);
                  setPath(item.path);
                  setFilename(item.scriptName);
                  setScenarioname("");
                }}
              >
                <PlusCircleOutlined 
                  title="申请添加场景"
                />
              </a>
            ]}
          >
            <List.Item.Meta
              avatar={<Avatar src={img}/>}
              title={<p className="list-title">{item.scriptName}</p>}
              description={dayjs(item.timestamp).format("YYYY-MM-DD HH:mm:ss")}
            />
            {item.apkName + " " + item.description}
          </List.Item>
        )}
      />
      <Modal
        title="跳转展示"
        visible={isModalVisible1}
        onCancel={handleCancel1}
        footer={null}
        centered={true}
        closeIcon={<CloseOutlined />}
        width="400px"
      >
        <img
          src={image} 
          width="350px"
        />
      </Modal>
      <Modal
        title="申请添加场景"
        visible={isModalVisible2}
        onCancel={handleCancel2}
        onOk={handleOk2}
        closable={false}
        centered={true}
      >
        <Form
          labelCol={{ span: 3 }}
          wrapperCol={{ span: 24 }}
        >
          <Form.Item
            label="App名"
          >
            <Input 
              className="input_apkname"
              value={apkname}
              size="middle"   
              disabled
            />
          </Form.Item>
          <Form.Item
            label="路径"
            rules={[{required : true}]}
          >
            <Input 
              className="input_path"
              value={path}
              size="middle"   
              disabled
            />
          </Form.Item>
          <Form.Item
            label="场景名"
            rules={[{required : true}]}
          >
            <Input 
              className="input_scenarioname"
              value={scenarioname}
              size="middle"   
              placeholder="请为您选择的路径设置场景名"
              onChange={
                (e)=>{
                    setScenarioname(e.target.value);
                }
            }
            />
          </Form.Item>
        </Form>
      </Modal>
    </div>
  );
}
export default connect((state) => {
  return { configure: state.configure };
})(Download);
