import { BrowserRouter, Switch, Link } from "react-router-dom";
import { Layout, Menu } from "antd";
import { connect } from "react-redux";
import { UserOutlined, NotificationOutlined } from "@ant-design/icons";
import UserInfo from "./UserInfo/UserInfo";
import UserNotify from "./UserNotify/UserNotify";
import AuthRoute from "../../utils/AuthRoute";
import Storage from "../../utils/Storage";

import "./User.css";
import { configure } from "@testing-library/dom";

const { Content, Sider } = Layout;

function Methods(props) {
  const { configure } = props;
  if (Storage.getItem("admin") === true) {
    return (
      <BrowserRouter>
        <Layout className="site-layout-background" style={{ padding: "0px 0px" }}>
          <Sider className="site-layout-background" width={200}>
            <Menu
              mode="vertical"
              defaultSelectedKeys={["1"]}
              style={{ height: "100%" }}
            >
              <Menu.Item key="1" icon={<UserOutlined />}>
                <Link to="/app/user/userinfo">账号信息</Link>
              </Menu.Item>
              {/* <Menu.Item key="2" icon={<NotificationOutlined />}>
                <Link to="/app/user/usernotify">我的消息</Link>
              </Menu.Item> */}
            </Menu>
          </Sider>
          <Content
            className="site-layout-background"
            style={{
              margin: "0px 16px",
              padding: 24,
              minHeight: 758,
            }}
          >
            <Switch>
              <AuthRoute path="/app/user/" exact component={UserInfo} />
              <AuthRoute
                path="/app/user/userinfo"
                exact
                component={UserInfo}
              />
             {/* <AuthRoute
                path="/app/user/usernotify"
                exact
                component={UserNotify}
              /> */}
            </Switch>
          </Content>
        </Layout>
      </BrowserRouter>
    );
  }
  else {
    return (
      <BrowserRouter>
        <Layout className="site-layout-background" style={{ padding: "0px 0px" }}>
          <Sider className="site-layout-background" width={200}>
            <Menu
              mode="vertical"
              defaultSelectedKeys={["1"]}
              style={{ height: "100%" }}
            >
              <Menu.Item key="1" icon={<UserOutlined />}>
                <Link to="/app/user/userinfo">账号信息</Link>
              </Menu.Item>
              <Menu.Item key="2" icon={<NotificationOutlined />}>
                <Link to="/app/user/usernotify">我的消息</Link>
              </Menu.Item>
            </Menu>
          </Sider>
          <Content
            className="site-layout-background"
            style={{
              margin: "0px 16px",
              padding: 24,
              minHeight: 758,
            }}
          >
            <Switch>
              <AuthRoute path="/app/user/" exact component={UserInfo} />
              <AuthRoute
                path="/app/user/userinfo"
                exact
                component={UserInfo}
              />
             <AuthRoute
                path="/app/user/usernotify"
                exact
                component={UserNotify}
              />
            </Switch>
          </Content>
        </Layout>
      </BrowserRouter>
    );
  }
}

export default connect((state) => {
  return { configure: state.configure };
})(Methods);
