import { useState, useEffect } from "react";
import { Input, Table, Button, notification } from "antd";
import { connect } from "react-redux";
import { ReloadOutlined } from "@ant-design/icons";
import Storage from "../../../utils/Storage";
import "./Scenario.css";

function Scenario(props) {
  const headers = {
    Accept: "application/json",
    "Content-Type": "application/json",
    "Access-Control-Allow-Origin": "*",
    "Access-Control-Allow-Headers":
      "Origin, X-Requested-With, Content-Type, Accept, Token",
    "Access-Control-Allow-Methods": "PUT,POST,GET,DELETE,OPTIONS",
    token: Storage.getItem("token"),
  };
  const { configure } = props;
  const { Column } = Table;
  const { Search } = Input;

  const IP = process.env.REACT_APP_SERVER_PORT;
  const [data, setData] = useState([]);
  const [filterData, setFilterData] = useState(data);

  useEffect(() => {
    fetch(IP + "/get_scenario/", {
      method: "get",
      headers: headers,
    })
      .then((res) => res.json())
      .then((newData) => {
        setData(newData.value);
        setFilterData(newData.value);
      })
      .catch((error) => console.log(error));
  }, [configure]);

  return (
    <div className="scenario page">
      <Button icon={<ReloadOutlined />} onClick={() => setFilterData(data)} />
      <Search
        className="search"
        placeholder="搜索场景名"
        onSearch={(e) =>
          setFilterData(data.filter((d) => d.scenario_name.indexOf(e) != -1))
        }
      />
      <Table className="scenario_table" dataSource={filterData}>
        <Column
          title="scenario_id"
          dataIndex="scenario_id"
          key="scenario_id"
          className="scenario_id"
        />
        <Column
          title="scenario_name"
          dataIndex="scenario_name"
          key="scenario_name"
          className="scenario_name"
        />
        <Column
          title="path"
          dataIndex="path"
          key="path"
          className="path"
          sorter={(a, b) => a.path.length - b.path.length}
        />
        <Column
          title="Action"
          key="action"
          className="action"
          render={(text, record) => (
            <a
              className="submit"
              onClick={(e) => {
                notification["info"]({
                  message: "开始生成",
                  description: "您可以继续发起其他任务，本任务完成后我们会通知您",
                  duration: 3,
                });
                const obj = { scenario_name: record.scenario_name };
                fetch(IP + "/scenario_download", {
                  method: "post",
                  body: JSON.stringify(obj),
                  headers: headers,
                })
                  .then((res) => res.json())
                  .then((news) => {
                    console.log(news);
                    notification["success"]({
                      message: "生成成功",
                      description: "请在下载管理页面下载所生成的脚本",
                      duration: 3,
                    });
                  })
                  .catch((error) => {
                    console.log(error);
                    notification["error"]({
                      message: "生成失败",
                      description: "请将以下错误原因反馈给管理员：" + error,
                      duration: 3,
                    });
                  });
              }}
            >
              Submit
            </a>
          )}
        />
      </Table>
    </div>
  );
}

export default connect((state) => {
  return { configure: state.configure };
})(Scenario);
