import { useEffect, useState } from "react";
import { connect } from "react-redux";
import { Select, Button, Form, notification } from "antd";
import "./SpecifiedPath.css";
import Storage from "../../../utils/Storage";

function SpecifiedPath(props) {
  const { configure } = props;
  const { Option } = Select;

  const IP = process.env.REACT_APP_SERVER_PORT;
  const headers = {
    Accept: "application/json",
    "Content-Type": "application/json",
    "Access-Control-Allow-Origin": "*",
    "Access-Control-Allow-Headers":
      "Origin, X-Requested-With, Content-Type, Accept, Token",
    "Access-Control-Allow-Methods": "PUT,POST,GET,DELETE,OPTIONS",
    token: Storage.getItem("token"),
  };

  const noIcon = <div></div>;
  const [nextList, setNextList] = useState([]);
  const [nodeList, setNodeList] = useState([]);

  useEffect(() => {
    let specifiedPath = Storage.getItem("specifiedPath");
    if (specifiedPath != undefined && specifiedPath.length != 0) {
      setNodeList(specifiedPath);
      const obj = { node_id: specifiedPath[specifiedPath.length - 1] };
      fetch(IP + "/get_childrens", {
        method: "post",
        body: JSON.stringify(obj),
        headers: headers,
      })
        .then((res) => res.json())
        .then((news) => {
          const { value } = news;
          setNextList(value);
        })
        .catch((error) => console.log(error));
    } else {
      setNextList([0]);
    }
  }, []);

  return (
    <Form className="specifiedPathForm" layout="vertical">
      <Form.Item className="nextListArea" label="选择节点：">
        <Select
          onSelect={(e) => {
            Storage.setItem("specifiedPath", [...nodeList, e], 60 * 60 * 1000);
            setNodeList([...nodeList, e]);
            const obj = { node_id: e };
            fetch(IP + "/get_childrens", {
              method: "post",
              body: JSON.stringify(obj),
              headers: headers,
            })
              .then((res) => res.json())
              .then((news) => {
                const { value } = news;
                setNextList(value);
              })
              .catch((error) => console.log(error));
          }}
        >
          {nextList.map((a) => (
            <Option key={a}>{a}</Option>
          ))}
        </Select>
      </Form.Item>
      <Form.Item>
        <p>生成路径：</p>
        <Select
          className="nodeListArea"
          mode="multiple"
          removeIcon={noIcon}
          showSearch={false}
          value={nodeList}
          dropdownClassName="hiddenDropdown"
        ></Select>
      </Form.Item>
      <Form.Item>
        <Button
          onClick={() => {
            Storage.setItem(
              "specifiedPath",
              [...nodeList.slice(0, -1)],
              60 * 60 * 1000
            );
            setNodeList([...nodeList.slice(0, -1)]);
            if (nodeList.length > 1) {
              const obj = { node_id: nodeList[nodeList.length - 2] };
              fetch(IP + "/get_childrens", {
                method: "post",
                body: JSON.stringify(obj),
                headers: headers,
              })
                .then((res) => res.json())
                .then((news) => {
                  const { value } = news;
                  setNextList(value);
                })
                .catch((error) => console.log(error));
            } else {
              setNextList([0]);
            }
          }}
        >
          back
        </Button>
        &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
        <Button
          htmlType="submit"
          className="backButton"
          type="primary"
          onClick={() => {
            if (nodeList.length > 0) {
              notification["info"]({
                message: "开始生成",
                description: "您可以继续发起其他任务，本任务完成后我们会通知您",
                duration: 3,
              });
              const obj = { points: nodeList };
              fetch(IP + "/path_generate", {
                method: "post",
                body: JSON.stringify(obj),
                headers: headers,
              })
                .then((res) => res.json())
                .then((news) => {
                  console.log(news);
                  notification["success"]({
                    message: "生成成功",
                    description: "请在下载管理页面下载所生成的脚本",
                    duration: 3,
                  });
                })
                .catch((error) => {
                  console.log(error);
                  notification["error"]({
                    message: "生成失败",
                    description: "请将以下错误原因反馈给管理员：" + error,
                    duration: 3,
                  });
                });
            } else {
              alert("路径不能为空！");
            }
            Storage.setItem("specifiedPath", [], 60 * 60 * 1000);
            setNodeList([]);
            setNextList([0]);
          }}
        >
          Submit
        </Button>
      </Form.Item>
    </Form>
  );
}

export default connect((state) => {
  return { configure: state.configure };
})(SpecifiedPath);
