import { useState, useEffect } from "react";
import { Modal, Button, Form, Input, Divider } from "antd";
import { EyeInvisibleOutlined, EyeTwoTone } from '@ant-design/icons';
import Storage from "../../../utils/Storage";
import "./UserInfo.css";

export default function UserInfo(props) {
    const [isModalVisible, setIsModalVisible] = useState(false);
    const [oldpassword, setOldpassword] = useState("");
    const [newpassword, setNewpassword] = useState("");
    const [confirmpassword, setConfirmpassword] = useState("");
    const [username, setUsername] = useState("")
    const token = Storage.getItem("token");

    const IP = process.env.REACT_APP_SERVER_PORT;
    const headers = {
        "Accept": "application/json",
        "Content-Type": "application/json",
        "Access-Control-Allow-Origin": "*",
        "Access-Control-Allow-Headers": "Origin, X-Requested-With, Content-Type, Accept, Token",
        "Access-Control-Allow-Methods": "PUT,POST,GET,DELETE,OPTIONS",
        "token": token
    };

    useEffect(() => {
        fetch(IP + "/userInfo", {
            method: "get",
            headers: headers,
        }).then((res) => res.json())
        .then((data) => {
            setUsername(data.value);
        });
    });

    const showModal = () => {
        setIsModalVisible(true);
    };

    const handleOk = () => {
        if(oldpassword == ""){
            alert("请输入原密码！");
        }
        else if(newpassword == ""){
            alert("请输入新密码！");
        }
        else if(confirmpassword == ""){
            alert("请确认你的密码！");
        }
        else if(newpassword != confirmpassword){
            alert("密码不匹配，请重新输入！");
        }
        else{
            const obj = {
                "prePassword": oldpassword,
                "newPassword": newpassword
            };
            fetch(IP + "/changepassword", {
                method: "post",
                body: JSON.stringify(obj),
                headers: headers
            }).then(res=>res.json())
            .then(news=>{
                const {value} = news;
                if(value == "success"){
                    alert("密码修改成功！");
                    setIsModalVisible(false);
                    setOldpassword("");
                    setNewpassword("");
                    setConfirmpassword("");
                }
                else if(value == "failure"){
                    alert("原密码不正确！");
                }
            })
            .catch(error=>console.log(error));
        } 
    };

    const handleCancel = () => {
        console.log(oldpassword);
        setIsModalVisible(false);
        setOldpassword("");
        setNewpassword("");
        setConfirmpassword("");
        
        console.log(oldpassword);
    };
    return (
        <div className="account-info">
            <div className="info-title">
                账号信息
            </div>
            <Divider />
            <div className="info-name">
                登录账号
            </div>
            <Input
                className="input-name"
                size="middle"
                placeholder={username}
                disabled
            />
            <div className="info-pswd">
                登录密码
            </div>
            <Input
                className="input-pswd"
                size="middle"
                placeholder="********"
                disabled
            />
            &nbsp;&nbsp;&nbsp;&nbsp;
            <Button
                className="btn-modify"
                type="primary"
                size="large"
                ghost
                onClick={showModal}
            >
                修改密码
            </Button>
            <Modal
                title="修改密码"
                visible={isModalVisible}
                onOk={handleOk}
                onCancel={handleCancel}
                okText="修改密码"
                cancelText="取消"
                closable={false}
                centered={true}
            >
                <Form
                    //name="specifiedNodeForm"
                    layout="vertical"
                >
                    <Form.Item
                        //name="oldpassword"
                        label="原密码"
                        rules={[{ required: true }]}
                    >
                        <Input.Password
                            className="input_oldpassword"
                            value={oldpassword}
                            size="large"   
                            placeholder="填写原密码"
                            onChange={
                                (e)=>{
                                    setOldpassword(e.target.value);
                                }
                            }
                            iconRender={visible => (visible ? <EyeTwoTone /> : <EyeInvisibleOutlined />)}
                        />
                    </Form.Item>
                    <Form.Item
                        //name="newpassword"
                        label="新密码"
                        rules={[{ required: true }]}
                    >
                        <Input.Password
                            className="input_newpassword"
                            value={newpassword}
                            onChange={
                                (e)=>{
                                    setNewpassword(e.target.value);
                                }
                            }
                            size="large"   
                            placeholder="新密码不能和原密码一致"
                            iconRender={visible => (visible ? <EyeTwoTone /> : <EyeInvisibleOutlined />)}
                        />
                    </Form.Item>
                    <Form.Item
                        //name="confirmpassword"
                        label="确认密码"
                        rules={[{ required: true }]}
                    >
                        <Input.Password
                            className="input_confirmpassword"
                            value={confirmpassword}
                            onChange={
                                (e)=>{
                                    setConfirmpassword(e.target.value);
                                }
                            }
                            size="large"   
                            placeholder="确认密码和新密码保持一致"
                            iconRender={visible => (visible ? <EyeTwoTone /> : <EyeInvisibleOutlined />)}
                        />
                    </Form.Item>
                </Form>
            </Modal>
        </div>
    )
}