import { Input, List, Button, Avatar, Modal, Form } from "antd";
import { useEffect, useState } from "react";
import { ReloadOutlined, CheckCircleOutlined, CloseCircleOutlined, FileGifOutlined, PlusCircleOutlined, CloseOutlined } from "@ant-design/icons";
import { connect } from "react-redux";
import "./ScenarioCheck.css";
import img from "../../images/download.png"
import Storage from "../../utils/Storage";
import dayjs from 'dayjs'

function ScenarioCheck(props) {
  const headers = {
    "Accept": "application/json",
    "Content-Type": "application/json",
    "Access-Control-Allow-Origin": "*",
    "Access-Control-Allow-Headers":
    "Origin, X-Requested-With, Content-Type, Accept, Token",
    "Access-Control-Allow-Methods": "PUT,POST,GET,DELETE,OPTIONS",
    "token": Storage.getItem("token"),
  };
  const [logId, setLogId] = useState(0);
  const [image, setImage] = useState("");
  const [isModalVisible1, setIsModalVisible1] = useState(false);
  const [isModalVisible2, setIsModalVisible2] = useState(false);
  const [downloadinfo, setDownloadinfo] = useState("");
  const [searchkey, setSearchkey] = useState("");
  const [apkname, setApkname] = useState("");
  const [path, setPath] = useState("");
  const [scenarioname, setScenarioname] = useState("");
  const { Search } = Input;
  const { configure } = props;
  const handleCancel1 = () => {
    setIsModalVisible1(false);
    setImage("");
  };
  const handleCancel2 = () => {
    setIsModalVisible2(false);
  };
  const handleOk2 = () => {
    if (scenarioname == ""){
      alert("场景名不能为空");
    }
    else{
      fetch(process.env.REACT_APP_SERVER_PORT + "/admin/accept/" + logId, {
        method: "get",
        headers: headers,
      })
        .then((res) => res.json())
        .then((data) => {
          console.log(data);
        });
      setDownloadinfo(downloadinfo.filter((d) => (d.apply_id) !== (logId)));
      setIsModalVisible2(false);
    }
  };
  console.log(headers);
  useEffect(() => {
    fetch(process.env.REACT_APP_SERVER_PORT + "/admin/loglist", {
      method: "get",
      headers: headers,
      
    })
      .then((res) => res.json())
      .then((data) => {
        console.log(data.value);
        setDownloadinfo(data.value);
      });
  }, []);

  return (
    <div>
      <List
        className="downloadManager"
        itemLayout="horizontal"
        pagination={{
          onChange: (page, pageSize) => {
            console.log(page, pageSize);
          },
          pageSize: 8,
          showSizeChanger: false,
          showQuickJumper: true,
        }}
        dataSource={downloadinfo}
        renderItem={(item) => (
            <List.Item
                actions={[
                    <a
                        key="scenario-display"
                        onClick={ () => {
                            setIsModalVisible1(true);
                            fetch(process.env.REACT_APP_SERVER_PORT + "/display/" + item.apk_name + "/" + item.script_name, {
                                method: "get",
                                headers: headers,
                            }).then((res) => res.text())
                            .then((data) => {
                                setImage("data:image/gif;base64,"+data);
                                //console.log(data);
                            });
                                console.log(image);
                                console.log(downloadinfo);
                        }}
                    >
                        <FileGifOutlined 
                            title="gif展示"
                        />
                    </a>,
                    <a
                        key="scenario-agree"
                        onClick={ () => {
                            setIsModalVisible2(true);
                            setApkname(item.apk_name);
                            setPath(item.apply_path);
                            setScenarioname(item.apply_descriptions);
                            setLogId(item.apply_id);
                        }}
                    >
                        <CheckCircleOutlined 
                            title="批准"
                        />
                    </a>,
                    <a
                        key="scenario-refuse"
                        onClick={() => {
                          fetch(process.env.REACT_APP_SERVER_PORT + "/admin/reject/" + item.apply_id, {
                            method: "get",
                            headers: headers,
                            
                          })
                            .then((res) => res.json())
                            .then((data) => {
                              console.log(data);
                            });
                          setDownloadinfo(downloadinfo.filter((d) => (d.apply_id) !== (item.apply_id)));
                        }}
                        //onClick
                    >
                        <CloseCircleOutlined 
                            title="拒绝"
                        />
                    </a>
                ]}
            >
            <List.Item.Meta
                avatar={<Avatar src={img}/>}
                title={<p className="list-title">{item.apply_descriptions}</p>}
                description={dayjs(item.apply_time).format("YYYY-MM-DD HH:mm:ss") + " by " + item.apply_user_name}
            />
                {item.apk_name + " " + item.apply_path}
            </List.Item>
        )}
      />
      <Modal
        title="跳转展示"
        visible={isModalVisible1}
        onCancel={handleCancel1}
        footer={null}
        centered={true}
        closeIcon={<CloseOutlined />}
        width="400px"
      >
        <img
          src={image} 
          width="350px"
        />
      </Modal>
      <Modal
        title="申请添加场景"
        visible={isModalVisible2}
        onCancel={handleCancel2}
        onOk={handleOk2}
        closable={false}
        centered={true}
      >
        <Form
          labelCol={{ span: 3 }}
          wrapperCol={{ span: 24 }}
        >
          <Form.Item
            label="App名"
          >
            <Input 
              className="input_apkname"
              value={apkname}
              size="middle"   
              disabled
            />
          </Form.Item>
          <Form.Item
            label="路径"
            rules={[{required : true}]}
          >
            <Input 
              className="input_path"
              value={path}
              size="middle"   
              disabled
            />
          </Form.Item>
          <Form.Item
            label="场景名"
            rules={[{required : true}]}
          >
            <Input 
              className="input_scenarioname"
              value={scenarioname}
              size="middle"   
              placeholder="请为您选择的路径设置场景名"
              onChange={
                (e)=>{
                    setScenarioname(e.target.value);
                }
            }
            />
          </Form.Item>
        </Form>
      </Modal>
    </div>
  );
}
export default connect((state) => {
  return { configure: state.configure };
})(ScenarioCheck);
